import React from 'react';
import { BASE_URL } from "../Utils/Constants";

const Categories = ({ filterCategory, categorieSelected}) => {

  const categories = [
    {id:0, name:'Todos', image:'/images/all-icon-01.png'},
    {id:1, name:'Frutas', image:'/images/all-icon-01.png'},
    {id:2, name:'Verduras', image:'/images/all-icon-02.png'},
    {id:3, name:'Cereales, semillas y secos', image:'/images/all-icon-03.png'},
    {id:4, name:'Cremería', image:'/images/all-icon-05.png'},
    {id:5, name:'Carne', image:'/images/all-icon-07.png'},
    {id:6, name:'Pollo', image:'/images/Pollo.png'},
    {id:7, name:'Abarrotes', image:'/images/all-icon-09.png'},
    {id:8, name:'Varios', image:'/images/all-icon-10.png'},
    {id:9, name:'Pescado', image:'/images/Pescado.png'},
    {id:10, name:'Favoritos', image:'https://s3-us-west-2.amazonaws.com/s.cdpn.io/9632/heart.png'}
  ];


  let fontSize = "17px";

  var mql = window.matchMedia("(orientation: portrait)");
  
  if(mql.matches) {  // Portrait orientation
    if(window.screen.height < 900){
      fontSize = "12px";
    }
  } else {  // Landscape orientation
    if(window.screen.height < 500){
      fontSize = "12px";
    }
  }

  return (
    <section className="naturalInner">
      <div className="container">
        <div className="row">
          <div className="headingOther">
            <h2>NUESTROS <span>PRODUCTOS</span></h2>
          </div>
        </div>
        <div className="row">
          <div className="contentnatural" style={{textAlign: "center"}}>
            <ul >
              {
                categories.map((category,index) => {
                  // const logo = logos.filter(logo => {
                  //   return parseInt(logo.categories_id) === parseInt(category.id) ? logo : '';
                  // })

                  // const path = logo.length > 0 ? BASE_URL +logo[0].image : '';

                  return (
                    <li key={index} className="wow fadeInLeft"
                      onClick={() => filterCategory(category.id)}>
                      <figure style={{ marginLeft: "10px", width: "50px", height: "50px"}}>
                        {/* <img className="img-responsive" src={category.id === 0 || category.id === 10 || category.id === 8 ? category.image : path} alt="" /> */}
                        <img className="img-responsive" src={ category.image} alt="" />
                      </figure>
                      <br/>

                      <article><p style={{fontSize:category.name !== "Cereales, semillas y secos" ? "17px"  :fontSize}}>{category.name} </p></article>
                      {/* <article><a href={category.id === 8 ? "#" :"/pedidos/" + category.id}><p>{category.name} </p></a></article> */}
                      <hr className={parseInt(categorieSelected) === category.id ? "product-selected" : "wow fadeInLeft"}
                        style={{ marginLeft: "10px"}} />
                    </li>
                  )
                })
              }
              <li key={13} className="wow fadeInLeft" />
              <li key={14} className="wow fadeInLeft" />
              <li key={15} className="wow fadeInLeft" />
              <li key={16} className="wow fadeInLeft" />
              <li key={17} className="wow fadeInLeft" />
            </ul>
          </div>
        </div>
      </div>
    </section>
  )
};

export default Categories;
